<template>
    <v-tab :items="tabItems">
        <div slot="generatecode">
            <v-input-txt label="/Url" placeholder="" v-model="dataTxt"></v-input-txt>
            
            <div class="layui-form-item layui-form-text">
                <label class="layui-form-label"></label>
                <div class="layui-input-inline">
                    <input type="text" class="layui-input" placeholder=" #ffffff" v-model="color"/>
                </div>
                <div class="layui-input-inline">
                    <input type="text" class="layui-input" placeholder=" #1aa094" v-model="bgcolor"/>
                </div>
            </div>
            <div class="layui-form-item">
                <div class="layui-input-block">
                    <v-button @click="convert(dataTxt,color,bgcolor)"></v-button>
                </div>
            </div>
            <div class="layui-form-item">
                <div class="layui-input-block">
                    <img :src="qrcodeImg" />
                </div>
            </div>
        </div>
        <div slot="decode">
           <div id="qrfile" qrCanvas="qr-canvas" qrResult="qrcode_content">
	            <div title="">
	                <div class="browser" id="canvas_ext_div">
		              <label style="height:30px;line-height:30px;">
		                <span></span>
		                <input type="file" name="files" accept="image/*" onchange="webqr.handleFiles(this.files);" title="Click to upload qrcode">
		              </label>
		            </div>
	            </div>
	            <canvas style="border: 1px solid #CCC;" id="qr-canvas" ></canvas>
	        </div>
			<div><p id="qrcode_content" style="border: 1px solid #ccc;line-height: 30px;"></p></div>
        </div>
    </v-tab>
</template>
<script>
import jrQrcode from 'jr-qrcode'
import common from '../utils/common'
export default {
    data(){
        return {
            tabItems:[
                {
                    Name:'generatecode',
                    Title:''
                },
                {
                    Name:'decode',
                    Title:''
                },
                {
                    Name:'source',
                    Title:'jrQrcode',
                    Url:'https://github.com/diamont1001/jrQrcode'
                },
                {
                    Name:'code',
                    Title:'',
                    Url:"http://coding.net/u/yimocoding/p/metools/git/blob/master/src/pages/qrcode.vue"
                }
            ],
            qrcodeImg:'',
            dataTxt:'',
            color:'',
            bgcolor:'',
            imgText:''
        }
    },
    created(){
        this.$nextTick(()=>{
            this.selectDecode();
        })
    },
    watch:{
        'dataTxt':'zidong'
    },
    methods:{
        zidong(){
            this.convert(this.dataTxt,this.color,this.bgcolor);
        },
        convert(txt,color,bgcolor){
            var imgBase64 = jrQrcode.getQrBase64(txt,{
                padding		: 10,   //10px
                width		: 256,  //256px
                height		: 256,  //256px
                correctLevel	: QRErrorCorrectLevel.H,    //level
                background      : color||"#ffffff",    //
                foreground      : bgcolor||"#1aa094"     //
            });
            this.qrcodeImg=imgBase64;
        },
        selectDecode(){
            if(typeof(webqr)!='undefined'){
                webqr.load('qrfile',200,200);
            }
            else{
                common.dynamicLoadJs('/static/qrcode/llqrcode.js',function(){
                    common.dynamicLoadJs('/static/qrcode/webqr.js',function(){
                        webqr.load('qrfile',200,200);
                    })
                });
            }
        }
    }

}
</script>


